.section {
    padding: $space 0;

    @include tablet {
        //Mixin de grid con la variable $columnas
        @include grid(2);
        column-gap: 3rem;
    }

    .section-text {
        display: flex;
        flex-direction: column;
        justify-content: center;

        h2 {
            margin-top: 5rem;
        }

        .date {
            color: $blue;
            font-weight: bold;
            padding-top: 5rem;
        }

        .text {
            line-height: 1.5;
        }
    }
}