.upbutton {
    height: 10rem;
    width: 10rem;
    border-radius: 5rem;
    border: solid 2px #000;
    display: block;
    background-color: $white;
    position: fixed;
    bottom: 0;
    right: 0;
    margin-right: 3rem;
    margin-bottom: 3rem;
    cursor: pointer;

    &:before {
        content: '';
        background-image: url('../img/arrow-up.png');  
        width: 9rem;
        height: 6rem;
        display: block;
        margin: 0 auto;
        background-repeat: no-repeat;
        background-position: center center;
    }

    &:hover {
        background-color: $grey;
        transition: ease-in .3s;
    }
}