@mixin phone() {
    @media (min-width: $phone) {
        @content;
    }
}

@mixin tablet() {
    @media (min-width: $tablet) {
        @content;
    }
}

@mixin desktop() {
    @media (min-width: $desktop) {
        @content;
    }
}

@mixin grid($columnas) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
}