html {
  font-size: 65.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

body.fijar-body {
  overflow: hidden;
}

a {
  font-size: 2rem;
  text-decoration: none;
}

p {
  color: 900;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-yellow {
  background-color: #FFA940;
}

.bg-green {
  background-color: #FC9C26;
}

.header {
  background-color: #FC9C26;
}

.header .contenido-header {
  padding: 2.5rem 0;
}

@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header h1 {
  color: #FFF;
  margin-bottom: 1rem;
  text-align: center;
}

@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.header.fixed {
  position: fixed;
  width: 100%;
  -webkit-box-shadow: -1px 6px 20px 2px #000000;
  box-shadow: -1px 6px 20px 2px #000000;
}

.navbar {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
}

.navbar a {
  text-decoration: none;
  color: #FFF;
  font-size: 2rem;
}

.navbar a:hover {
  color: #0077B0;
  transition: ease-in-out .5s;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}

.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#050101+0,f9a039+67,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(5, 1, 1, 0.8) 0%, rgba(249, 160, 57, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(5, 1, 1, 0.8) 0%, rgba(249, 160, 57, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(5, 1, 1, 0.8) 0%, rgba(249, 160, 57, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc050101', endColorstr='#80ffffff',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}

.video video {
  display: block;
  width: 100%;
}

.video .contenido-video {
  color: #FFF;
  display: flex;
  align-items: center;
  height: 100%;
}

.video .contenido-video h2 {
  display: flex;
  flex-direction: column;
  font-size: 5rem;
  align-items: flex-start;
}

.video .contenido-video span {
  font-size: 2rem;
  background-color: #0077B0;
  padding: 1rem;
  margin-top: 1rem;
}

.section {
  padding: 5rem 0;
}

@media (min-width: 768px) {
  .section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
}

.section .section-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section .section-text h2 {
  margin-top: 5rem;
}

.section .section-text .date {
  color: #0077B0;
  font-weight: bold;
  padding-top: 5rem;
}

.section .section-text .text {
  line-height: 1.5;
}

.lineup {
  background-color: #0077B0;
  padding: 5rem 0;
}

.lineup h3, .lineup p {
  color: #FFF;
  text-align: center;
}

.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .stages-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
}

.stages-container .stage {
  padding-top: 2.5rem;
}

.stages-container .stage-name {
  text-align: center;
  color: #000;
  font-size: 2rem;
  font-weight: 300;
}

.stages-container .stage-name:after {
  content: '';
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-repeat: no-repeat;
  background-position: center center;
}

.hiphop .stage-name:after {
  background-image: url("../img/hip-hop.png");
}

.rap .stage-name:after {
  background-image: url("../img/rap.png");
}

.calendar {
  padding: 0;
  list-style: none;
}

.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}

.calendar li span {
  font-weight: 400;
}

.bg-green .calendar li:nth-child(odd) {
  background-color: #ec8303;
}

.bg-yellow .calendar li:nth-child(odd) {
  background-color: #ff920d;
}

.gallery {
  padding: 5rem 0;
}

.gallery h3 {
  text-align: center;
  margin-bottom: 5rem;
}

.gallery .gallery-img {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.66667rem;
}

@media (min-width: 768px) {
  .gallery .gallery-img {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1200px) {
  .gallery .gallery-img {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  background-color: #FFF;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  display: grid;
  place-items: center;
  font-weight: 700;
  font-size: 3rem;
}

.btn-cerrar:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}

.tickets h3 {
  text-align: center;
  color: #FFF;
}

.tickets .ticket-container {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.tickets .plan p {
  color: #FFF;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.ticket {
  width: 100%;
  border-spacing: 0;
}

.ticket thead {
  background-color: #0077B0;
}

.ticket thead th {
  color: #FFF;
  padding: 1.66667rem;
  text-transform: uppercase;
  font-weight: 300;
}

.ticket tbody {
  background-color: #FFF;
}

.ticket tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #e1e1e1;
}

.ticket tbody tr:last-of-type {
  border: none;
}

.ticket tfoot td {
  color: 900;
  background-color: #C9C9C9;
  padding: 1.25rem;
  text-align: center;
}

.footer {
  background-color: #0077B0;
  padding: 1.25rem;
  text-align: center;
  color: #FFF;
}

.footer p {
  margin: 0;
}

.upbutton {
  height: 10rem;
  width: 10rem;
  border-radius: 5rem;
  border: solid 2px #000;
  display: block;
  background-color: #FFF;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 3rem;
  margin-bottom: 3rem;
  cursor: pointer;
}

.upbutton:before {
  content: '';
  background-image: url("../img/arrow-up.png");
  width: 9rem;
  height: 6rem;
  display: block;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-position: center center;
}

.upbutton:hover {
  background-color: #C9C9C9;
  transition: ease-in .3s;
}
