.header {
    background-color: $orange;

    .contenido-header {
        padding: $space/2 0;

        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }    
    }
       
    h1 {
        color: $white;
        margin-bottom: 1rem;
        text-align: center;

        @include tablet {
            margin-bottom: 0;
        }
    }

    &.fixed {
        position: fixed;
        width: 100%;
        -webkit-box-shadow: -1px 6px 20px 2px #000000; 
        box-shadow: -1px 6px 20px 2px #000000;
    }
}

.navbar {
    display: flex;
    gap: $space/2;
    justify-content: center;

    a {
        text-decoration: none;
        color: $white;
        font-size: 2rem;

        &:hover {
            color: $blue;
            transition: ease-in-out .5s;
        }
    }
}