.video {
    position: relative;
    max-height: 70rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#050101+0,f9a039+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg,  rgba(5,1,1,0.8) 0%, rgba(249,160,57,0.6) 67%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(5,1,1,0.8) 0%,rgba(249,160,57,0.6) 67%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(5,1,1,0.8) 0%,rgba(249,160,57,0.6) 67%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc050101', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }

    video {
        display: block;
        width: 100%;
    }

    .contenido-video {
        color: $white;
        display: flex;
        align-items: center;
        height: 100%;

        h2 {
            display: flex;
            flex-direction: column;
            font-size: 5rem;
            align-items: flex-start;
        }

        span {
            font-size: 2rem;
            background-color: $blue;
            padding: 1rem;
            margin-top: 1rem;
            
        }
    }
}

