.tickets {
    padding: $space 0;

    h3 {
        text-align: center;
        color: $white;
    }

    .ticket-container {
        margin-top: 2.5rem;
        @include grid(2);
        gap: 2rem;
    }

    .plan {
        p {
            color: $white;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }
}

.ticket {
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $blue;

        th {
            color: $white;
            padding: $space / 3;
            text-transform: uppercase;
            font-weight: $thin;
        }
    }

    tbody {
        background-color: $white;

        td {
            padding: $space / 4;
            border-bottom: 1px solid #e1e1e1;
        }

        tr {
            &:last-of-type {
                border: none;
            }
        }
    }

    tfoot {
        td {
            color: $black;
            background-color: $grey;
            padding: $space / 4;
            text-align: center;
        }
    }
}