.gallery {
    padding: $space 0;

    h3 {
        text-align: center;
        margin-bottom: $space;
    }

    .gallery-img {
        list-style: none;
        padding: 0;

        @include grid(2);
        gap: $space / 3;

        @include tablet {
            @include grid(4);
        }

        @include desktop {
            @include grid(6);
        }
    }
}

.overlay {
    position: fixed;
    background-color: rgba($color: #000000, $alpha: .5);
    width: 100%;
    height: 100%;
    top: 0;

    //Con estas lineas centramos las fotos
    display: grid;
    place-items: center;
}

.btn-cerrar {
    background-color: $white;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    display: grid;
    place-items: center;
    font-weight: $bold;
    font-size: 3rem;

    &:hover {
        cursor: pointer;
    }
}