.lineup {
    background-color: $blue;
    padding: $space 0;

    h3, p {
        color: $white;
        text-align: center;
    }

    .day {
        text-transform: uppercase;
        margin: $space 0;
    }
}

.stages-container {
    @include tablet {
        @include grid(2);
        column-gap: 3rem;
    }

    .stage {
        padding-top: $space / 2;
    }

    .stage-name {
        text-align: center;
        color: #000;
        font-size: 2rem;
        font-weight: $thin;

        &:after {
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}


.hiphop .stage-name {
    &:after {
        background-image: url('../img/hip-hop.png');   
    }
}

.rap .stage-name {
    &:after {
        background-image: url('../img/rap.png');
    }
}

.calendar {
    padding: 0;
    list-style: none;

    li {
        padding: $space / 2;
        font-weight: $bold;

        span {
            font-weight: $regular;
        }
    }
}

//Oscurece los li que son impares
.bg-green .calendar li:nth-child(odd) {
    background-color: darken($orange, 10);
}

.bg-yellow .calendar li:nth-child(odd) {
    background-color: darken($secondaty-orange, 10);
}